import React, { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faBars,
  faAngleDown,
  faAngleUp,
} from "@fortawesome/free-solid-svg-icons";
import { faFile, faEnvelopeOpen } from "@fortawesome/free-regular-svg-icons";
import { faGithubAlt, faLinkedinIn } from "@fortawesome/free-brands-svg-icons";

class Navbar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name1: "",
      name2: "",
      name3: "",
      name4: "",
      name5: "",
      name6: "",
      translate1: 0,
      contact: false,
    };
    this.handleScroll = this.handleScroll.bind(this);
    this.handleContactMe = this.handleContactMe.bind(this);
  }

  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll, { passive: true });
    this.setState({ name1: <pre> &nbsp;&nbsp;</pre> });
    this.setState({ name2: "matt" });
    this.setState({ name3: <pre> mars</pre> });
    this.setState({ name4: "den" });
    this.setState({ name5: "" });
    this.setState({ name6: "" });
    this.setState({ contact: false });
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  handleScroll(event) {
    let scrollTop = Math.round(window.scrollY),
      itemTranslate = Math.round(scrollTop / 3);
    if (itemTranslate < 5) {
      this.setState({ name1: <pre>&nbsp;&nbsp; </pre> });
      this.setState({ name2: "matt" });
      this.setState({ name3: <pre> mars</pre> });
      this.setState({ name4: "den" });
      this.setState({ name5: " " });
      this.setState({ name6: " " });
      this.setState({ translate1: 0 });
    } else if (itemTranslate < 40) {
      this.setState({ name1: "<" });
      this.setState({ name2: "m" });
      this.setState({ name3: <pre>att </pre> });
      this.setState({ name4: "marsden" });
      this.setState({ name5: "89/" });
      this.setState({ name6: ">" });
      this.setState({ translate1: itemTranslate / 10 });
    } else if (itemTranslate < 180) {
      this.setState({ name1: "<" });
      this.setState({ name2: "m" });
      this.setState({ name3: <pre>att </pre> });
      this.setState({ name4: "marsden" });
      this.setState({ name5: "89/" });
      this.setState({ name6: ">" });
      this.setState({ translate1: itemTranslate / 5 });
    } else if (itemTranslate > 181) {
      this.setState({ name1: "<" });
      this.setState({ name2: "m" });
      this.setState({ name3: <pre>att </pre> });
      this.setState({ name4: "marsden" });
      this.setState({ name5: "89/" });
      this.setState({ name6: ">" });
      this.setState({ translate1: 181 / 5 });
    }
  }

  handleContactMe() {
    const { contact } = this.state;
    this.setState({ contact: !contact });
    this.forceUpdate();
  }

  render() {
    const contactHtml = (
      <div className={this.state.contact ? "menu-drop" : "menu-drop-close"}>
        <div className="empty-space"></div>
        <div className="menu-mobile-sty">
          <a
            href="https://github.com/mmarsden89"
            target="_blank"
            rel="noopener noreferrer"
          >
            <FontAwesomeIcon icon={faFile} className="m-icon" />
            resume
          </a>
          <a
            href="https://github.com/mmarsden89"
            target="_blank"
            rel="noopener noreferrer"
          >
            <FontAwesomeIcon icon={faGithubAlt} className="m-icon" />
            github
          </a>
          <a
            href="https://www.linkedin.com/in/matthewjmarsden/"
            target="_blank"
            rel="noopener noreferrer"
          >
            <FontAwesomeIcon icon={faLinkedinIn} className="m-icon" />
            linkedin
          </a>
          <a
            href="mailto:mattmarsdendev@gmail.com"
            target="_blank"
            rel="noopener noreferrer"
          >
            <FontAwesomeIcon icon={faEnvelopeOpen} className="m-icon" />
            email
          </a>
        </div>
      </div>
    );

    const { contact } = this.state;
    return (
      <nav className="navbar">
        <div className="nav-container">
          <div className="nav-name">
            <h1 onScroll={this.handleScroll} className="nav-text ">
              {this.state.name1}
            </h1>
            <h1 onScroll={this.handleScroll} className="nav-text open">
              {this.state.name2}
            </h1>
            <h1
              onScroll={this.handleScroll}
              style={{ marginLeft: `-${this.state.translate1}px` }}
            >
              {this.state.name3}
            </h1>
            <h1
              onScroll={this.handleScroll}
              style={{ backgroundColor: "#282c34" }}
            >
              {this.state.name4}
            </h1>
            <h1 onScroll={this.handleScroll} className="numbers">
              {this.state.name5}
            </h1>
            <h1 onScroll={this.handleScroll} className="nav-text close">
              {this.state.name6}
            </h1>
          </div>
          <div className="nav-link-container">
            <a
              className="nav-link"
              target="_blank"
              href="img/Matt_Marsden_Resume.pdf"
            >
              resume
            </a>
            <a href={"#work"} className="nav-link">
              work
            </a>
            <a href={"#portfolio"} className="nav-link">
              portfolio
            </a>
            <div
              className={contact ? "contact-me-menu-active" : "contact-me-menu"}
              onClick={this.handleContactMe}
            >
              <span>
                <p className="nav-link">contact me!</p>
                <FontAwesomeIcon icon={faBars} className="hamburger-icon" />
                <FontAwesomeIcon icon={faAngleDown} className="angle" />
              </span>
              {contact && contactHtml}
            </div>
          </div>
        </div>
      </nav>
    );
  }
}

export default Navbar;
